import React from 'react'
import { Route, Switch, withRouter } from 'react-router-dom'

import Home from './pages/Home'
import Stores from '../stores/pages/index'
import Users from '../users/pages/index'
import Suppliers from '../suppliers/pages/index'
import DistributionCenters from '../distributionCenters/pages/index'
import Fleets from '../fleet/pages/index'

const Routes = () => {
  return (
    <Switch>
      <Route exact path="/" component={Home} />
      <Route path="/users" component={Users} />
      <Route path="/stores" component={Stores} />
      <Route path="/suppliers" component={Suppliers} />
      <Route path="/distributioncenters" component={DistributionCenters} />
      <Route path="/fleet" component={Fleets} />
    </Switch>
  )
}

export default withRouter(Routes)
